<template>
  <div>
    <h2 class="generator-name">2D to 3D Conversion</h2>

    <el-row type="flex" justify="space-between">
      <el-col :span="9" class="generate-conditon">
        <el-form
          :model="formValues"
          ref="form1"
          :rules="formRules"
          size="small"
          label-position="left"
          @submit.stop
        >
          <FormBlock prop="imageURL" class="update-block">
            <h4 class="update-title">2D Images</h4>

            <UploadImage
              upload-position="center"
              upload-tips="Upload photos"
              image-size="306px"
            ></UploadImage>
          </FormBlock>

          <ViewBlock>
            <el-form-item label-width="70px" label="Quantity" prop="number">
              <div class="radio-group-wrap">
                <el-radio-group v-model="formValues.number">
                  <el-radio-button
                    v-for="item in numberOptions"
                    :key="item"
                    :label="item"
                  >
                  </el-radio-button>
                </el-radio-group>
              </div>
            </el-form-item>

            <p class="bottom-buttons bottom-buttons--left">
              <el-button type="primary" class="generate-button">
                Generate with one click
              </el-button>
            </p>

            <el-form-item prop="agree">
              <div class="checkbox-wrap">
                <el-checkbox v-model="formValues.agree">
                  Agree to the
                </el-checkbox>

                <UserRulesDialog
                  v-model="showUserRulesDialog"
                  @agree="handleDialogAgree"
                >
                  <strong
                    :class="formValues.agree && 'rule-button--actived'"
                    class="rule-button"
                    @click="handleUserRulesClick"
                  >
                    &nbsp;AI Privacy Agreement
                  </strong>
                </UserRulesDialog>
              </div>
            </el-form-item>
          </ViewBlock>
        </el-form>
      </el-col>

      <el-col :span="15" class="generate-images">
        <el-row type="flex" justify="center">
          <el-skeleton
            :loading="generateLoading"
            :animated="generateLoading"
            class="generated-image"
          >
            <template #template>
              <el-skeleton-item variant="image" />
            </template>

            <template>
              <img
                v-if="generatedList.length > 0"
                :src="selectedIamgeUrl"
                class="mosaic-bg"
              />
            </template>
          </el-skeleton>
        </el-row>

        <ViewBlock>
          <el-row type="flex" class="generated-list-content">
            <el-col
              v-for="i in 4"
              :span="6"
              :key="i"
              class="generated-list-img-content"
            >
              <div class="img-content">
                <img v-if="generatedList[i - 1]" :src="generatedList[i - 1]" />
              </div>
            </el-col>

            <el-col :span="24" class="bottom-buttons bottom-buttons--right">
              <el-button
                size="small"
                class="prompt-button"
                @click="randomPrompt"
              >
                Share
              </el-button>

              <el-button
                size="small"
                class="prompt-button"
                @click="createPrompt"
              >
                Download
              </el-button>
            </el-col>
          </el-row>
        </ViewBlock>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import FormBlock from '@/components/FormBlock.vue'
import UploadImage from '@/components/UploadImage.vue'
import ViewBlock from '@/components/ViewBlock.vue'
import UserRulesDialog from '@/components/UserRulesDialog.vue'

export default {
  components: { FormBlock, UploadImage, ViewBlock, UserRulesDialog },

  data () {
    return {
      formValues: {
        imageURL: null,
        number: 1,
        agree: false
      },

      formRules: {},

      numberOptions: [1, 2, 3, 4],

      showUserRulesDialog: false,

      generateLoading: false,

      generatedList: [],

      selectedIamgeUrl: ''
    }
  },

  methods: {
    randomPrompt () {},

    createPrompt () {},

    handleUserRulesClick () {
      this.showUserRulesDialog = true
    },

    handleDialogAgree () {
      this.formValues.agree = true
    }
  }
}
</script>

<style lang="less" scoped>
@import "@/styles/variable.less";

.generator-name {
  font-size: 32px;
  color: #270066;
  line-height: 42px;
  text-align: left;
  margin-bottom: 24px;
}

.generate-conditon {
  padding-right: 30px;
  max-width: 412px;
}

.generate-images {
  padding-left: 30px;
  max-width: 700px;
}

.update-block {
  border: 3px solid transparent;
  background-clip: padding-box, border-box;
  background-origin: padding-box, border-box;
  background-image: linear-gradient(to right, #fff, #fff),
    linear-gradient(
      142deg,
      rgba(194, 178, 255, 1),
      rgba(235, 158, 255, 1),
      rgba(192, 251, 255, 1)
    );
}

.update-title {
  font-size: 16px;
  text-align: center;
}

.el-icon-question {
  color: #a28ffa;
}

.prompt-button {
  width: 106px;
  height: 36px;
  background: linear-gradient(312deg, #bbaff6 0%, #ebe2ff 100%);
  border-radius: 18px;
  color: #a28ffa;
  font-size: 14px;

  &:hover,
  &:focus {
    border-color: #a28ffa;
    color: #a28ffa;
  }
}

.radio-group-wrap {
  text-align: right;

  .el-radio-button + .el-radio-button {
    margin-left: 8px;
  }

  ::v-deep .el-radio-button {
    .el-radio-button__inner {
      width: 34px;
      height: 30px;
      background: linear-gradient(312deg, #bbaff6 0%, #ebe2ff 100%);
      border-radius: 20px;
      color: #b2b2b2;
      padding: 0;
      line-height: 30px;
      font-size: 14px;
    }

    &.is-active {
      .el-radio-button__inner {
        border: 2px solid #a28ffa;
        box-shadow: none;
      }
    }
  }
}

.bottom-buttons {
  text-align: center;

  .el-button + .el-button {
    margin-left: 30px;
  }

  &--left {
    margin-top: 34px;
    margin-bottom: 10px;
  }

  &--right {
    margin-top: 20px;
  }
}

.generate-button {
  width: 300px;
  height: 50px;
  border-radius: 30px;
  font-size: 16px;
}

.checkbox-wrap {
  text-align: center;
  color: #8f8f8f;

  .el-checkbox {
    color: #8f8f8f;

    ::v-deep .el-checkbox__input.is-focus .el-checkbox__inner {
      border-color: #000;
    }

    ::v-deep .el-checkbox__inner {
      &::after {
        border-color: #000;
      }

      &:hover {
        border-color: #000;
      }
    }

    &.is-checked {
      ::v-deep .el-checkbox__inner {
        background-color: transparent;
        border-color: #000;
      }

      ::v-deep .el-checkbox__label {
        color: #000;
      }
    }
  }
}

.rule-button {
  cursor: pointer;

  &:hover {
    color: @color-primary;
  }

  &.rule-button--actived {
    color: #000;
  }
}

.generated-image {
  width: 670px;
  height: 670px;
  border-radius: 16px;
  margin-bottom: 28px;
  background: no-repeat center / 176px 193px url("~@/assets/image-wait.png"),
    linear-gradient(134deg, #f4f1fe 0%, #faf9ff 100%);
  display: flex;
  justify-content: center;
  align-items: center;

  img,
  .el-skeleton__image {
    width: 648px;
    height: 648px;
    border-radius: 16px;
    margin: 0 auto;
  }
}

.generated-list-content {
  flex-wrap: wrap;
}

.generated-list-img-content {
  max-width: 64px;

  & + & {
    margin-left: 16px;
  }
}

.img-content {
  width: 64px;
  height: 64px;
  border-radius: 12px;
  overflow: hidden;
  background: linear-gradient(134deg, #f4f1fe 0%, #faf9ff 100%);

  img {
    width: 100%;
    height: 100%;
    cursor: pointer;
  }
}
</style>
